import httpbase from '@/components/utils/http.js';
import {doCreateUser,getRegCode} from '@/api/supervisor/manager.js'

var tha,js;
export default {
	data() {
		return {
			phoneno:'',
			second:0,
			code:"",
			showPassword:false,
			password:'',
			str:'',
			havecode:false
		}
	},
	onLoad(){
		tha = this;
	},
	computed:{
		yanzhengma(){
			if(this.second==0){
				return '获取验证码';
			}else{
				if(this.second<10&&this.second>=0){
					return '重新获取0'+this.second;
				}else if(this.second>=10){
					return '重新获取'+this.second;
				}else{
					return '重新获取';
				}
			}
		}
	},
	methods: {
		display() {
		    this.showPassword = !this.showPassword
		},
		getcode(){
			var that=this;
			if(this.phoneno==''){
				uni.showToast({
				    icon: 'none',
				    title: '请输入正确的手机号'
				});
				return;
			}
		
			if(this.havecode)
			{
				uni.showToast({title:"请等待！"});
				return;
			}
			this.havecode=true;
			// this.havecode=false;
			//到计时
			
			this.second = 60;
			js = setInterval(function(){
				tha.second--;
				if(tha.second<=0){
					that.havecode=false;
					clearInterval(js)
				}
			},1000)
			
			getRegCode(this.phoneno).then(res=>{
				if(res.data.code==200)
				{
					this.str=res.data.data.str;
					uni.showToast({title:res.data.message});
					
				}else{
					uni.showToast({title:res.data.message,icon:'none'});
				}
			}).catch(error=>{
		    console.log(error)
		  });
		
		},

		bindLogin() {
			if (this.phoneno.length != 11) {
			     uni.showToast({
			        icon: 'none',
			        title: '手机号不正确'
			    });
			    return;
			}
		    if (this.password.length < 6) {
		        uni.showToast({
		            icon: 'none',
		            title: '密码不正确'
		        });
		        return;
		    }
			if (this.code.length != 4) {
			    uni.showToast({
			        icon: 'none',
			        title: '验证码不正确'
			    });
			    return;
			}
			if (this.code.toLowerCase() != this.str.toLowerCase()) {
			    uni.showToast({
			        icon: 'none',
			        title: '验证码不正确'
			    });
			    return;
			}
			let data1= {
					phone:this.phoneno,
					password:this.password,
					code:this.code
				};
				console.log(data1);
			uni.request({
			    url: httpbase.baseUrl+'api/v1/app/SupervisorManage/SetNewPass',
			    data: {
					phone:this.phoneno,
					password:this.password,
					code:this.code
				},
				method: 'POST',
				dataType:'json',
			    success: (res) => {
					if(res.data.code!=200){
						uni.showToast({title:res.data.msg,icon:'none'});
					}else{
						uni.showToast({title:res.data.msg});
						setTimeout(function(){
							uni.navigateBack();
						},1500) 
					}
			    }
			});
			
		}
	}
}
